@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 0;
  background-color: #e7f8f8;
}

.centered {
  margin: 3rem auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.focused {
  font-size: 3rem;
  font-weight: bold;
  color: white;
}

.btn {
  text-decoration: none;
  background-color: rgb(0, 151, 244);
  color: white;
  border-radius: 4px;
  padding: 0.75rem 1.5rem;
  border: 1px solid rgb(0, 151, 244);
  cursor: pointer;
}

.btn:hover,
.btn:active {
  background-color: rgb(1,40,61);
  border-color: rgb(1,40,61);
}

.btn--flat {
  cursor: pointer;
  font: inherit;
  color: rgb(0, 151, 244);
  border: none;
  background-color: none;
  text-decoration: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
}

.btn--flat:hover,
.btn--flat:active {
  background-color: rgb(0, 151, 244);
  color: white;
}